<template>
  <section id="body">
    <div class="container">
      <MainMenu :active-tab="activeTab" @change-tab="activeTab = $event" />
      <Profile :is-page-active="activeTab !== 'home'" v-bind="profileData" />
      <transition name="slide">
        <Resume
          v-if="activeTab === 'resume'"
          v-bind="resumeData"
          @go-to-contact="activeTab = 'contact'"
        />
        <Skills v-else-if="activeTab === 'skills'" :skills="me.skills" />
        <Portfolio
          v-else-if="activeTab === 'portfolio'"
          :portfolio="me.portfolio"
        />
        <Contact v-else-if="activeTab === 'contact'" v-bind="contactData" />
      </transition>
    </div>
  </section>
</template>

<script>
import pick from "lodash/pick";
import MainMenu from "../components/resume/MainMenu";
import Profile from "../components/resume/Profile";
import Resume from "../components/resume/Resume";
import Skills from "../components/resume/Skills";
import Portfolio from "../components/resume/Portfolio";
import Contact from "../components/resume/Contact";

export default {
  name: "ResumeView",
  components: {
    MainMenu,
    Profile,
    Resume,
    Skills,
    Portfolio,
    Contact
  },
  props: {
    me: {
      type: Object,
      default:() => ({})
    }
  },
  data: () => ({
    activeTab: "home",
  }),
  computed: {
    profileData() {
      return pick(this.me, [
        "displayName",
        "avatar",
        "email",
        "phone",
        "shortIntroduction",
        "socialProfiles"
      ]);
    },
    resumeData() {
      return pick(this.me, [
        'education',
        'employment',
        'resumeUrl'
      ])
    },
    contactData() {
      return pick(this.me, ["email", "address", "phone"]);
    }
  },
};
</script>

<style lang="less" scoped>
.slide-enter,
.slide-leave-to {
  @media @laptop {
    position: absolute;
    left: 30%;
    z-index: 11;
    opacity: 0;
  }
}
</style>
