<template>
  <div id="id">
    <ResumeView v-if="!isLoading" :me="me" />
    <Loader v-else />
  </div>
</template>

<script>
import firebase from "firebase/app";
import "firebase/database";
import isEmpty from "lodash/isEmpty";
import ResumeView from "./views/ResumeView";
import Loader from "./components/Loader";

export default {
  name: "App",
  components: {
    ResumeView,
    Loader
  },
  data: function() {
    return {
      me: {}
    };
  },
  computed: {
    isLoading() {
      return isEmpty(this.me);
    }
  },
  async mounted() {
    const snapshot = await firebase
      .database()
      .ref("/")
      .once("value");
    this.me = snapshot.val();
  }
};
</script>

