<template>
  <div
    id="page-skills"
    class="section-vcardbody section-page section-page-active"
  >
    <div class="section-skills">
      <h2 class="section-title">SKILLS</h2>

      <h3 class="section-item-title-2">
        <i class="fa fa-users"></i>&nbsp;&nbsp; Professional Skills
      </h3>

      <SkillList :skills="professionalSkills" />

      <h3 class="section-item-title-2">
        <i class="fa fa-code"></i>&nbsp;&nbsp; Code Skills
      </h3>

      <SkillList :skills="codeSkills" />

      <h3 class="section-item-title-2">
        <i class="fa fa-laptop"></i>&nbsp;&nbsp; Software Skills
      </h3>

      <SkillList :skills="softwareSkills" />
    </div>
  </div>
</template>

<script>
import SkillList from './SkillList'
export default {
  name: "Skills",
  components: {
    SkillList
  },
  props: {
    skills: {
      type: Array,
      default() {
        return [];
      }
    }
  },
  computed: {
    professionalSkills() {
      return this.skills.filter(sk => sk.group === "professional");
    },
    codeSkills() {
      return this.skills.filter(sk => sk.group === "code");
    },
    softwareSkills() {
      return this.skills.filter(sk => sk.group === "software");
    }
  }
};
</script>

