<template>
  <div class="container">
    <b-card title="Employment">
      <b-form @submit="onSubmit">
        <div v-for="project in profile.employment" :key="project.project">
          <b-row align-v="center">
            <b-col>
              <b-form-group label="Company" :label-for="project.project + '-input-company'">
                <b-form-input
                  :id="project.project+ '-input-company'"
                  v-model="project.company"
                  type="text"
                  required
                  placeholder="Company"
                ></b-form-input>
              </b-form-group>
            </b-col>
            <b-col>
              <b-form-group label="Position" :label-for="project.project + '-input-position'">
                <b-form-input
                  :id="project.project+ '-input-position'"
                  v-model="project.position"
                  type="text"
                  required
                  placeholder="Position"
                ></b-form-input>
              </b-form-group>
            </b-col>
          </b-row>
          <b-row>
            <b-col>
              <b-form-group label="Project" :label-for="project.project + '-input-project'">
                <b-form-input
                  :id="project.project+ '-input-project'"
                  v-model="project.project"
                  type="text"
                  required
                  placeholder="Project"
                ></b-form-input>
              </b-form-group>
            </b-col>
            <b-col>
              <b-form-group label="Team size" :label-for="project.project + '-input-teamSize'">
                <b-form-input
                  :id="project.project+ '-input-teamSize'"
                  v-model="project.teamSize"
                  type="number"
                  required
                  placeholder="Team size"
                ></b-form-input>
              </b-form-group>
            </b-col>
          </b-row>

          <b-row>
            <b-col>
              <b-form-group label="From" :label-for="project.project+'-datepicker-fromTime'">
                <b-form-datepicker
                  :id="project.project+'-datepicker-fromTime'"
                  v-model="project.fromTime"
                ></b-form-datepicker>
              </b-form-group>
            </b-col>
            <b-col>
              <b-form-group label="To" :label-for="project.project+'-datepicker-toTime'">
                <b-form-datepicker
                  :id="project.project+'-datepicker-toTime'"
                  v-model="project.toTime"
                  placeholder="Current working here"
                  reset-button
                ></b-form-datepicker>
              </b-form-group>
            </b-col>
          </b-row>

          <b-form-group label="Description" :label-for="project.project+'-input-description'">
            <b-form-textarea
              :id="project.project+ '-input-description'"
              v-model="project.description"
              required
              placeholder="Description"
              rows="4"
            ></b-form-textarea>
          </b-form-group>
          <b-form-group
            label="Responsibilities"
            :label-for="project.project+'-input-responsibilities'"
          >
            <b-form-textarea
              :id="project.project+ '-input-responsibilities'"
              v-model="project.responsibilities"
              required
              placeholder="Responsibilities"
              rows="4"
            ></b-form-textarea>
          </b-form-group>
          <b-form-group label="Technologies" :label-for="project.project+'-input-technologies'">
            <Multiselect
              :id="project.project+ '-input-technologies'"
              v-model="project.technologies"
              :options="technologies"
              taggable
              multiple
              :searchable="false"
              hide-selected
              class="technologies-select"
              :option-height="80"
              track-by="name"
            >
              <template slot="tag" slot-scope="props">
                <b-badge :style="`background-color: ${props.option.color}`" class="pl-2 pr-1 mr-2">
                  <i :class="props.option.icon"></i>
                  <span class="ml-2">{{props.option.name}}</span>
                  <b-button class="close-btn" @click="props.remove(props.option)">
                    <i class="fa fa-times"></i>
                  </b-button>
                </b-badge>
              </template>
              <template slot="option" slot-scope="props">
                <b-badge :style="`background-color: ${props.option.color}`" class="p-1 pl-2">
                  <i :class="props.option.icon"></i>
                  <span class="ml-2">{{props.option.name}}</span>
                </b-badge>
              </template>
            </Multiselect>
          </b-form-group>
          <hr />
        </div>
        <b-button type="submit" variant="primary">
          Submit
          <b-spinner v-if="submitting" small role="status"></b-spinner>
        </b-button>
        <b-button type="reset" variant="danger">Reset</b-button>
      </b-form>
    </b-card>
  </div>
</template>

<script>
import firebase from "firebase/app";
import "firebase/database";
import Multiselect from "vue-multiselect";
import technologies from "../utils/technologies";
export default {
  components: {
    Multiselect
  },
  data() {
    return {
      technologies,
      profile: {},
      submitting: false
    };
  },
  async mounted() {
    const snapshot = await firebase
      .database()
      .ref("/")
      .once("value");
    this.profile = snapshot.val();
  },
  methods: {
    async onSubmit(evt) {
      try {
        this.submitting = true;
        evt.preventDefault();
        await firebase
          .database()
          .ref("/")
          .set(this.profile);
        this.submitting = false;
        this.$router.push("/");
      } catch (error) {
        console.error(error);
      }
    }
  }
};
</script>
<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>
<style lang="less">
.container {
  margin-top: 1rem;
}
.close-btn {
  background: transparent;
  border: 0;
  margin: 0 0 0 4px;
  padding: 0 6px;
  border-radius: 4px;
  &:hover {
    background-color: rgba(0, 0, 0, 0.2);
  }
}
.technologies-select {
}
</style>